.eventos-slider-section {
    overflow: hidden;
  }
  
  .eventos-slider-section .evento-slide {
    width: 100%;
    height: 420px;
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: flex-end;
  }
  
  .eventos-slider-section .evento-overlay {
    background: rgba(0, 0, 0, 0.55);
    color: #fff;
    padding: 20px;
    width: 100%;
  }
  
  .eventos-slider-section .evento-titulo {
    font-size: 1.8rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 8px;
  }
  
  .eventos-slider-section .evento-info {
    font-size: 1rem;
    margin-bottom: 10px;
    color: #fff;
  }
  
  .eventos-slider-section .evento-descripcion {
    font-size: 0.95rem;
    margin-bottom: 0;
  }
  
  .eventos-slider-section .swiper-button-next, .swiper-button-prev {
    color: #fff;
  }
  
  .eventos-slider-section .swiper-pagination-bullet {
    background: #fff;
  }
  


  .eventos-slider-section .section-title::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 3px;
    background: linear-gradient(90deg, #e1306c, #f77737);
  }